    .activity > li,
    .fancybox-nav span,
    .ui-slider-range,
    .fc-button,
    .dropdown-submenu > a:after,
    .nav-pills > li > a:after,
    .nav-pills .label,
    .nav-tabs .label,
    .dropdown-menu > li > a > i,
    .dropdown-menu .label,
    .dropdown-menu > li > a > img,
