@font-face {
    font-family : "Flaticon";
    src         : url("#{$general-font-path}Flaticon.eot");
    src         : url("#{$general-font-path}Flaticon.eot?#iefix") format("embedded-opentype"), url("#{$general-font-path}Flaticon.woff") format("woff"), url("#{$general-font-path}Flaticon.ttf") format("truetype"), url("#{$general-font-path}Flaticon.svg#Flaticon") format("svg");
    font-weight : normal;
    font-style  : normal;
}

@media screen and (-webkit-min-device-pixel-ratio : 0) {
    @font-face {
        font-family : "Flaticon";
        src         : url("#{$general-font-path}Flaticon.svg#Flaticon") format("svg");
    }

}

.fi:before {
    display                 : inline-block;
    font-family             : "Flaticon";
    font-style              : normal;
    font-weight             : normal;
    font-variant            : normal;
    line-height             : 1;
    text-decoration         : inherit;
    text-rendering          : optimizeLegibility;
    text-transform          : none;
    -moz-osx-font-smoothing : grayscale;
    -webkit-font-smoothing  : antialiased;
    font-smoothing          : antialiased;
}

