/* bg color */
h3.form-title {
  box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.2);
  padding: 20px 10px;
  text-align: center;
  font-size: 26px;
  color: #ffffff;
  background: #00c0ef;
  border-radius: 0;
  margin-bottom: 0;
}

.login {
  background-color: #364150 !important;
}

.login .checkbox {
  margin-top: 0;
}

.login .logo {
  margin: 0 auto;
  margin-top: 60px;
  padding: 15px;
  text-align: center;
}

.login .content {
  background-color: white;
  width: 450px;
  margin: 10% auto 0 auto;
  overflow: hidden;
  position: relative;
}

.login .content .has-feedback label ~ .form-control-feedback {
  line-height: 35px;
}

.login .content h4 {
  color: #555555;
}

.login .content .hint {
  color: #999999;
  padding: 0;
  margin: 15px 0 7px 0;
}

.login .content .content-wrapper {
  padding: 30px;
}

.login .content .login-form,
.login .content .forget-form {
  padding: 0;
  margin: 0;
}

.login .content .form-control {
  background-color: #dde3ec;
  height: 43px;
  color: #8290a3;
  border: 1px solid #dde3ec;
}

.login .content .form-control:focus, .login .content .form-control:active {
  border: 1px solid #c3ccda;
}

.login .content .form-control::-moz-placeholder {
  color: #8290a3;
  opacity: 1;
}

.login .content .form-control:-ms-input-placeholder {
  color: #8290a3;
}

.login .content .form-control::-webkit-input-placeholder {
  color: #8290a3;
}

.login .content select.form-control {
  padding-left: 9px;
  padding-right: 9px;
}

.login .content .form-title {
  font-weight: 300;
}

.login .content .btn {
  margin: 0;
}

.login .content .login-options {
  margin-top: 30px;
  overflow: hidden;
}

.login .content .login-options h4 {
  float: left;
  font-weight: 600;
  font-size: 15px;
  color: #7d91aa !important;
}

.login .content .login-options .social-icons {
  float: right;
  padding-top: 3px;
}

.login .content .login-options .social-icons li a {
  border-radius: 15px 15px 15px 15px !important;
  -moz-border-radius: 15px 15px 15px 15px !important;
  -webkit-border-radius: 15px 15px 15px 15px !important;
}

.login .content .form-actions .checkbox {
  margin-left: 0;
  padding-left: 0;
}

.login .content .forget-form .form-actions {
  border: 0;
  margin-bottom: 0;
  padding-bottom: 20px;
}

.login .content .form-actions .btn {
  margin-top: 1px;
}

.login .content .form-actions .btn {
  font-weight: 600;
  padding: 10px 20px !important;
}

.login .content .form-actions .btn-default {
  font-weight: 600;
  padding: 10px 25px !important;
  color: #6c7a8d;
  background-color: #ffffff;
  border: none;
}

.login .content .form-actions .btn-default:hover {
  background-color: #fafaff;
  color: #45b6af;
}

.login .content .forget-password {
  font-size: 14px;
  float: right;
  display: inline-block;
  margin-top: 10px;
}

.login .content .create-account {
  margin: 0 -40px -30px -40px;
  padding: 15px 0 17px 0;
  text-align: center;
  background-color: #6c7a8d;
}

.login .content .create-account > p {
  margin: 0;
}

.login .content .create-account p a {
  font-weight: 600;
  font-size: 14px;
  color: #c3cedd;
}

.login .content .create-account a {
  display: inline-block;
  margin-top: 5px;
}

/* footer copyright */
.login .copyright {
  text-align: center;
  margin: 0 auto 30px 0;
  padding: 10px;
  color: #7a8ca5;
  font-size: 13px;
}

@media (max-width: 440px) {
  /***
    Login page
    ***/
  .login .logo {
    margin-top: 10px;
  }
  .login .content {
    width: 280px;
    margin-top: 10px;
  }
  .login .content h3 {
    font-size: 22px;
  }
  .forget-password {
    display: inline-block;
    margin-top: 20px;
  }
  .login-options .social-icons {
    float: left;
    padding-top: 3px;
  }
  .login .checkbox {
    font-size: 13px;
  }
}

.display-hide {
  display: none;
}

/***
Social Icons
***/
.social-icons {
  padding: 0;
  margin: 0;
}

.social-icons li {
  float: left;
  display: inline;
  list-style: none;
  margin-right: 5px;
  margin-bottom: 5px;
  text-indent: -9999px;
}

.social-icons li > a {
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  width: 28px;
  height: 28px;
  display: block;
  background-position: 0 0;
  background-repeat: no-repeat;
  transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
}

.social-icons li:hover > a {
  background-position: 0 -38px;
}

.social-icons li .facebook {
  background: url("/vendor/core/images/social/facebook.png") no-repeat;
}

.social-icons li .github {
  background: url("/vendor/core/images/social/github.png") no-repeat;
}

.social-icons li .googleplus {
  background: url("/vendor/core/images/social/googleplus.png") no-repeat;
}

.social-icons.social-icons-color > li > a {
  opacity: 0.7;
  background-position: 0 -38px !important;
}

.social-icons.social-icons-color > li > a:hover {
  opacity: 1;
}

.social-icons.social-icons-circle > li > a {
  border-radius: 25px !important;
}

/***
Inline Social Icons
***/
.social-icon {
  display: inline-block !important;
  width: 28px;
  height: 28px;
  background-position: 0 0;
  background-repeat: no-repeat;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
